/* DEFINITIONS */
$verdana-family: 'Verdana', sans-serif;


@font-face {
    
    font-family: 'Verdana';
    src: url('../assets/font/40474_VerdanaBold.ttf');
    src: url('../assets/font/Verdana\ Bold.ttf');
    src: url('../assets/font/Verdana.ttf');
    
}
$pink-color: rgb(237, 21, 255);
$blue-color: rgb(0, 74, 227);
$green-color: rgb(21, 209, 0);
$rect-width: 36vw; 
$rect-height: 27vw; // set to vw temporary for easy testing Should be vh§
/* final position for rects in middle from top and right */
$rect-width-tablet: 431px; // because the media screen is small we set welcome rects to big and stacked
$rect-width-tablet-static: 70vw; // static page contains text and should fill a large portion of the screen
$rect-height-tablet: 333px; // in order to stack we set height to small. vw instead of vh because scaling issues else hardcode
$rect-height-tablet-static: 60vh;
$top-middle:  250px;
$right-middle: 10vw;
$left-middle: 10vw;
$top-middle-tablet:  20vh;
$right-middle-tablet: 20vw;
$left-middle-tablet: calc(50% - (#{$rect-width-tablet-static})/2);

$blue-zindex: 60;
$blue-raster-zindex: 50;

$green-zindex: 40;
$green-raster-zindex: 30;

$pink-zindex: 20;
$pink-raster-zindex: 10;

/* Blue final in welcome position */
$blue-top-welcome-final: 45vh;
$blue-right-welcome-final: 25vw;
$blue-left-welcome-final: calc(100vw - (#{$rect-width} + #{$blue-right-welcome-final}));
/* Blue final in welcome position (TABLET) */
$blue-top-welcome-final-tablet: 61vh; // same calc as pink with start of 44;
$blue-right-welcome-final-tablet: 43vw;
$blue-left-welcome-final-tablet: 14vw;

/* Pink final in welcome position */
$pink-top-welcome-final: 15vh;
$pink-left-welcome-final: 7vw;
/* Pink final in welcome position (TABLET) */
$pink-top-welcome-final-tablet: 12vh; // calculated 15 + (height of rect) 34 - (overlapping) 5 = 44;
$pink-left-welcome-final-tablet: 5vw;

/* Green final in welcome position */
$green-top-welcome-final: 0vh;
$green-right-welcome-final: 5vw;
$green-left-welcome-final: calc(100vw - (#{$rect-width} + #{$green-right-welcome-final}));
/* Green final in welcome position (TABLET) */
$green-top-welcome-final-tablet: 36vh; // give some space to menu
$green-right-welcome-final-tablet: 5vw;
$green-left-welcome-final-tablet: calc(100vw - #{$rect-width-tablet} - #{$pink-left-welcome-final-tablet});;
//calc(100vw - #{$rect-width-tablet} - #{$pink-left-welcome-final-tablet}); <= 40vw;

// Raster pos
// RASTER WELCOME PLACES //
$raster-offset: 50px;
//BLUE raster//
$blue-raster-top-welcome-final-tablet: calc(#{$blue-top-welcome-final-tablet} + #{$raster-offset});
$blue-raster-left-welcome-final-tablet: calc(#{$blue-left-welcome-final-tablet} + #{$raster-offset});

//PINK raster//
$pink-raster-top-welcome-final-tablet: calc(#{$pink-top-welcome-final-tablet} + #{$raster-offset});
$pink-raster-left-welcome-final-tablet: calc(#{$pink-left-welcome-final-tablet} + #{$raster-offset});

//GREEN raster//
$green-raster-top-welcome-final-tablet: calc(#{$green-top-welcome-final-tablet} + #{$raster-offset});
$green-raster-left-welcome-final-tablet: calc(#{$green-left-welcome-final-tablet} + #{$raster-offset});


//animations
$slow-welcome-anim: ease-in-out all 700ms;
$fast-exit-anim: ease-in-out all 250ms;


// TABLET SETTINGS
// #######################################//
// #######################################//
// #########       TABLET         ########//
// #######################################//
// #######################################//



@media screen and (min-width: 768px) and (max-width: 1025px) {
    body{
        overflow: visible !important;
    }
    .rect-anim {
        position: absolute;
        width: $rect-width-tablet;
        min-height: $rect-height-tablet;
    }

    .green {
        position: absolute;
        background-color: $green-color;
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: $green-zindex;
    }
    /*--------------*/
    /* Welcome animation*/
    .green-transition-enter,
    .green-transition-appear {
        top: $green-top-welcome-final-tablet;
        left: -100vw;
        z-index: $green-zindex;
    }
    .green-transition-enter-active,
    .green-transition-appear-active {
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: $green-zindex;
        transition: $slow-welcome-anim;
    }
    .green-transition-enter-done,
    .green-transition-appear-done { 
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: $green-zindex;
        transition: $slow-welcome-anim;
    }
    
    .green-transition-exit {
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: $green-zindex;
    }
    .green-transition-exit-active {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: $green-zindex;
        transition: $slow-welcome-anim;
    }
    
    /* Gather green on top*/
    .green-transition-ontop-enter,
    .green-transition-ontop-appear {
        .information-text{
            color: transparent;
        }
        .link-row{
            display: none;
        }
        height: $rect-height-tablet;
        width: $rect-width-tablet;
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: 100;

    }
    .green-transition-ontop-enter-active,
    .green-transition-ontop-appear-active {
        .information-text{
            color: transparent;
        }
        .link-row{
            display: none;
        }
        min-height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: 100;
        transition: $fast-exit-anim;
    }
    .green-transition-ontop-enter-done,
    .green-transition-ontop-appear-done {
        .information-text{
            color: white;
        }
        .link-row{
            display: flex;
        }
        min-height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: 100;
        transition: $fast-exit-anim;
    }
    
    /* Gather green under*/
    .green-transition-under-enter,
    .green-transition-under-appear {
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: $green-zindex;
        background-color: $green-color;
    }
    .green-transition-under-enter-active,
    .green-transition-under-appear-active {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $green-color;
        z-index: -3;
        transition: $fast-exit-anim;
    }
    .green-transition-under-enter-done,
    .green-transition-under-appear-done {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $green-color;
        z-index: -3;
        transition: $fast-exit-anim;
    }
    
    /*--------------*/
    /* Pink rect */
    .pink {
        position: absolute;
        background-color: $pink-color;
        top: $pink-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        z-index: $pink-zindex;
    }
    .pink-transition-enter,
    .pink-transition-appear {
        top: $pink-top-welcome-final-tablet;
        left: -47vw;
        z-index: $pink-zindex;
    }
    .pink-transition-enter-active,
    .pink-transition-appear-active {
        top: $pink-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        transition: $slow-welcome-anim;
        z-index: $pink-zindex;
    }
    .pink-transition-enter-done,
    .pink-transition-appear-done {
        top: $pink-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        transition: $slow-welcome-anim;
        z-index: $pink-zindex;
    }
    .pink-transition-exit {
        top: $pink-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        z-index: $pink-zindex;
    }
    .pink-transition-exit-active {
        top: $top-middle-tablet;
        left: $right-middle-tablet;
        transition: $slow-welcome-anim;
    }
    /* Gather pink on top*/
    .pink-transition-ontop-enter,
    .pink-transition-ontop-appear {
        .information-text{
            color: transparent;
        }
        .link-row{
            display: none;
        }
        height: $rect-height-tablet;
        width: $rect-width-tablet;
        top: $pink-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        z-index: 100;
        height: $rect-height-tablet;
    }
    .pink-transition-ontop-enter-active,
    .pink-transition-ontop-appear-active {
        .information-text{
            color: transparent;
        }
        .link-row{
            display: none;
        }
        min-height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: 100;
        transition: $fast-exit-anim;
    }
    .pink-transition-ontop-enter-done,
    .pink-transition-ontop-appear-done {
        .information-text{
            color: white;
        }
        .link-row{
            display: flex;
        }
        min-height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: 100;
        transition: $fast-exit-anim;
    }
    
    /* Gather pink under*/
    .pink-transition-under-enter,
    .pink-transition-under-appear {
        top: $pink-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        background-color: $pink-color;
        z-index: $pink-zindex;
    }
    .pink-transition-under-enter-active,
    .pink-transition-under-appear-active {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $pink-color;
        transition: $fast-exit-anim;
        z-index: $pink-zindex;
    }
    .pink-transition-under-enter-done,
    .pink-transition-under-appear-done {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $pink-color;
        transition: $fast-exit-anim;
        z-index: $pink-zindex;
    }
    
    /*--------------*/
    /* Blue rect */
    .blue {
        position: absolute;
        background-color: $blue-color;
        top: $blue-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        z-index: $blue-zindex;
    }
    .blue-middle {
        background-color: $blue-color;
        top: $top-middle-tablet;
        right: $right-middle-tablet;
    }
    .blue-transition-enter,
    .blue-transition-appear {
        top: $blue-top-welcome-final-tablet;
        left: -100vw;
    }
    .blue-transition-enter-active,
    .blue-transition-appear-active {
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
        transition: $slow-welcome-anim;
    }
    .blue-transition-enter-done,
    .blue-transition-appear-done {
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
        transition: $slow-welcome-anim;
    }
    .blue-transition-exit {
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
    
    }
    .blue-transition-exit-active {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
    
        transition: $slow-welcome-anim;
    }
    .blue-transition-exit-done {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
    }
    
    /* Gather blue on top*/
    .blue-transition-ontop-enter,
    .blue-transition-ontop-appear {
        .information-text{
            color: transparent;
            height: 1px;
        }
        height: $rect-height-tablet;
        width: $rect-width-tablet;
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
    }
    .blue-transition-ontop-enter-active,
    .blue-transition-ontop-appear-active {
        .information-text{
            color: transparent;
            height: 2px;
        }
        min-height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        transition: $fast-exit-anim;
    }

    .blue-transition-ontop-enter-done,
    .blue-transition-ontop-appear-done {
        .information-text{
            height: 100%;
            color: white;
        }
        min-height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        transition: $fast-exit-anim;
    }
    
    /* Gather blue under*/
    .blue-transition-under-enter,
    .blue-transition-under-appear {
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
    }
    .blue-transition-under-enter-active,
    .blue-transition-under-appear-active {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        transition: $fast-exit-anim;
    }
    .blue-transition-under-enter-done,
    .blue-transition-under-appear-done {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        transition: $fast-exit-anim;
    }

    /*------------*/
    /* Static Rectangles */
    .static-blue-rect {
        position: absolute;
        min-height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $blue-color;
        z-index: 99;
    }

    .static-pink-rect {
        position: absolute;
        min-height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $pink-color;
        z-index: 99;
    }
    .static-green-rect {
        position: absolute;
        min-height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $green-color;
        z-index: 99;
    }

    .static-page {
        padding: 10% 9%;
    }

    .information-text {
        position: relative;
        font-size: 1.1rem;
        font-family: $verdana-family;
        font-weight: bold;
        color: white;
        text-align: left;
        z-index: 100;
        ///padding: 25% 10%;
    }

    #text-row {
        min-height: 250px;
    }

    .img-wrapper {
        width: 100%;
        height: 100%;
    }

    .logo-image {
        margin-left: 3vw;
        margin-top: 3vw;
        width: 200px;
        height: auto;
        position: relative;
        z-index: 120;
    }

    .display {
        background-color: whitesmoke;
        position: absolute;
        top: 0;
        left: 0;
    }

    .static-page-link{
        color: white !important; 
        text-align: left;
        font-size: 1.1rem;
        font-weight: bold;
        z-index: 100;
        font-family: $verdana-family;
        border-bottom: solid 2px white;
        border-radius: 0px;
    }
    .link-col{
        height: 180px;
    }
    .icon-pic{
        width: 170px;
        height: auto;
    }
    .icon-pic-filminstitutet{
        width: 100px;
        height: auto;
    }
    .icon-pic-kultur{
        width: 150px;
        padding-right: 20px;
        height: auto;
    }
    .menu-box{
        position: absolute;
        color: black;
        font-size: 40px;
        top: 0vh;
        left: 0vw;
        width: 50vw;
        height: 100vh;
        z-index: 100;
        background-color: white;
    }
    .menu-items{
        margin: 0 auto;
        top: 20vh;
        color: black;
        justify-content: center;
        vertical-align: auto;
        list-style: none;
        flex-direction: horizontal;
        align-items: center;
        text-align: center;
    }
    .visable-xs{
        visibility: visible;
    }
    .spacer{
        height: 0px;
        width: 0px;
    }
    .hamburger-icon{
        visibility: hidden;
    }
