    }
    .green-raster-transition-ontop-enter-active,
    .green-raster-transition-ontop-appear-active {
        height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: calc(#{$top-middle-tablet} + #{$raster-offset});
        left: calc(#{$left-middle-tablet} + #{$raster-offset});
        z-index: $green-raster-zindex;
        transition: $fast-exit-anim;
    }
    .green-raster-transition-ontop-enter-done,
    .green-raster-transition-ontop-appear-done {
