/* Portrait */
@media only screen 
  and (min-device-width: 800px) 
  and (max-device-width: 825px) 
  and (orientation: landscape) 
  {
    .information-text{
        font-size: 11px;
    }
    .static-page-link{
        font-size: 11px;
    }
    .link-row{
        padding-bottom: 10px;
    }
}

/* Portrait */
@media only screen 
  and (min-device-width: 1020px) 
  and (max-device-width: 1024px) 
  and (orientation: portrait) 
  and (-webkit-min-device-pixel-ratio: 2) {

    .information-text{
        font-size:  1.4rem;
    }
    .static-page-link{
        font-size: 1.4rem;
    }
    .icon-pic-filminstitutet{
        width: 150px;
        height: auto;
    }
    .icon-pic-kultur{
        width: 160px;
        padding-right: 20px;
        height: auto;
    }
    .btn {
        padding-top: 25px;
        font-size: 25px;
    }
}

/* Landscape */
@media only screen 
  and (min-width: 1360px) 
  and (max-width: 1370px) 
  and (orientation: landscape) 
  and (-webkit-min-device-pixel-ratio: 2)
 {
