.btn {
  position: relative;
  background-color: transparent;
  border: none;
  font-size: 25px;
  font-family: $verdana-family;
  font-weight: bold;
  padding: 5px;
  margin-right: 50px;
  color: white;
  :active{
      border: none;
      border-radius: 0px;
  }
  :hover{
      border: none;
  }
  z-index: 100;
  :focus{
  outline: 0 !important;
  }
}
.button-blue {
  position: relative;
  background-color: transparent;
  border: none;
  font-family: $verdana-family;
  font-weight: bold;
  padding: 5px;
  margin-right: 50px;
  color: white;
  :focus{
      outline:0;
  }
  z-index: 100;
}
.button-pink {
  position: relative;
  background-color: transparent;
  border: none;
  font-family: $verdana-family;
  font-weight: bold;
  padding: 5px;
  margin-right: 50px;
  color: white;
  :focus{
      outline:0;
  }
  z-index: 100;
}
.button-green {
  position: relative;
  background-color: transparent;
  border: none;
  font-family: $verdana-family;
  font-weight: bold;
  padding: 5px;
  margin-right: 50px;
  color: white;
  z-index: 100;
}
.active {
  border-radius: 0px;
  border-bottom: solid 4px white;
}

/*------------*/
/* Static Rectangles */
.static-blue-rect {
  position: absolute;
  top: $top-middle;
  left: $left-middle;
  background-color: $blue-color;
  z-index: 90;
}

.static-pink-rect {
  position: absolute;
  top: $top-middle;
  left: $left-middle;
  background-color: $pink-color;
  z-index: 90;
}
.static-green-rect {
  position: absolute;
  top: $top-middle;
  left: $left-middle;
  background-color: $green-color;
  z-index: 90;
}

/* Video */
.background-video {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -3;
}

