body {
  background-color: #000000;
  color: black;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


li {
  color: white;
}

.content {
  background-color: blue;
  padding: 100px;
  width: 1000px;
  height: 890px;
  display: inline-block;
}

.title {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  color: #61dafb;
  font-weight: 400;
}

