/*-- Skeleton --*/
/*---------------
.class {
    positionfromtop: where;
    positionfromleft: where;
    size: _;
    size: _;
    color: _;
    background-color: _;
    transition: prop duration;
}
----------------*/

$verdana-family: 'Verdana', sans-serif;


@font-face {
    
    font-family: 'Verdana';
    src: url('../assets/font/40474_VerdanaBold.ttf');
    src: url('../assets/font/Verdana\ Bold.ttf');
    src: url('../assets/font/Verdana.ttf');
    
}
    
$pink-color: rgb(237, 21, 255);
$blue-color: rgb(0, 74, 227);
$green-color: rgb(21, 209, 0);
$rect-height: 31vw; // set to vw temporary for easy testing Should be vh§
$rect-width: calc(#{$rect-height} * 1.3); 
/* final position for rects in middle from top and right */
$rect-width-tablet: 80vw; // because the media screen is small we set welcome rects to big and stacked
$rect-width-tablet-static: 80vw; // static page contains text and should fill a large portion of the screen
$rect-height-tablet: 60vw; // in order to stack we set height to small. vw instead of vh because scaling issues else hardcode
$rect-height-tablet-static: 70vh;
$top-middle:  20vh;
$right-middle: 32vw;
$left-middle: 32vw;
$top-middle-tablet:  20vh;
$right-middle-tablet: 10vw;
$left-middle-tablet: 10vw;

/* Raster zindex */
$blue-zindex: 60;
$blue-raster-zindex: 50;

$green-zindex: 40;
$green-raster-zindex: 30;

$pink-zindex: 20;
$pink-raster-zindex: 10;

/* Blue final in welcome position */
$blue-top-welcome-final: 55.5vh;
$blue-right-welcome-final: 24vw;
$blue-left-welcome-final: calc(100vw - (#{$rect-width} + #{$blue-right-welcome-final}));
/* Blue final in welcome position (TABLET) */
$blue-top-welcome-final-tablet: 73vh; // same calc as pink with start of 44;
$blue-right-welcome-final-tablet: 5vw;
$blue-left-welcome-final-tablet: 7vw;

/* Pink final in welcome position */
$pink-top-welcome-final: 15vh;
$pink-left-welcome-final: 7vw;
/* Pink final in welcome position (TABLET) */
$pink-top-welcome-final-tablet: 44vh; // calculated 15 + (height of rect) 34 - (overlapping) 5 = 44;
$pink-left-welcome-final-tablet: 10vw;

/* Green final in welcome position */
$green-top-welcome-final: 0vh;
$green-right-welcome-final: 8vw;
$green-left-welcome-final: calc(100vw - (#{$rect-width} + #{$green-right-welcome-final}));
/* Green final in welcome position (TABLET) */
$green-top-welcome-final-tablet: 15vh; // give some space to menu
$green-right-welcome-final-tablet: 5vw;
$green-left-welcome-final-tablet: 35vw;


// RASTER WELCOME PLACES //
$raster-offset: 3vw;
//BLUE raster//
$blue-raster-top-welcome-final: calc(#{$blue-top-welcome-final} + #{$raster-offset});
$blue-raster-left-welcome-final: calc(#{$blue-left-welcome-final} + #{$raster-offset});

//PINK raster//
$pink-raster-top-welcome-final: calc(#{$pink-top-welcome-final} + #{$raster-offset});
$pink-raster-left-welcome-final: calc(#{$pink-left-welcome-final} + #{$raster-offset});

//GREEN raster//
$green-raster-top-welcome-final: calc(#{$green-top-welcome-final} + #{$raster-offset});
$green-raster-left-welcome-final: calc(#{$green-left-welcome-final} + #{$raster-offset});

//animations
$slow-welcome-anim: ease-in-out all 700ms;
$fast-exit-anim: ease-in-out all 250ms;




@media screen and (min-width: 1200px) and (max-width:2499px) {
/*--------------*/
/* Rect define */
.rect-anim {
    width: $rect-width;
    min-height: $rect-height;
}

/*--------------*/
/* Green rect */
.green {
    position: absolute;
    background-color: $green-color;
    top: $green-top-welcome-final;
    left: $green-left-welcome-final;
    z-index: $green-zindex;
}
/*--------------*/
/* Welcome animation*/
.green-transition-enter,
.green-transition-appear {
    top: -100vh;
    left: $green-raster-left-welcome-final;
    z-index: $green-zindex;
}
.green-transition-enter-active,
.green-transition-appear-active {
    top: $green-top-welcome-final;
    left: $green-left-welcome-final;
    z-index: $green-zindex;
    transition: $slow-welcome-anim;
}
.green-transition-enter-done,
.green-transition-appear-done { 
    top: $green-top-welcome-final;
    left: $green-left-welcome-final;
    z-index: $green-zindex;
    transition: $slow-welcome-anim;
}
.green-transition-exit {
    top: $green-top-welcome-final;
    left: $green-left-welcome-final;
    z-index: $green-zindex;
}
.green-transition-exit-active {
    top: $top-middle;
    left: $left-middle;
    transition: $slow-welcome-anim;
    z-index: $green-zindex;
}

/* Gather green on top*/
.green-transition-ontop-enter,
.green-transition-ontop-appear {
    top: $green-top-welcome-final;
    left: $green-left-welcome-final;
    z-index: 90;
}
.green-transition-ontop-enter-active,
.green-transition-ontop-appear-active {
    top: $top-middle;
    left: $left-middle;
    z-index: 90;
    transition: $fast-exit-anim;
}
.green-transition-ontop-enter-done,
.green-transition-ontop-appear-done {
    top: $top-middle;
    left: $left-middle;
    z-index: 90;
    transition: $fast-exit-anim;
}

/* Gather green under*/
.green-transition-under-enter,
.green-transition-under-appear {
    top: $green-top-welcome-final;
    left: $green-left-welcome-final;
    background-color: $green-color;
    z-index: $green-zindex;
}
.green-transition-under-enter-active,
.green-transition-under-appear-active {
    top: $top-middle;
    left: $left-middle;
    background-color: $green-color;
    z-index: $green-zindex;
    transition: $fast-exit-anim;
}
.green-transition-under-enter-done,
.green-transition-under-appear-done {
    top: $top-middle;
    left: $left-middle;
    background-color: $green-color;
    z-index: $green-zindex;
    transition: $fast-exit-anim;
}

/*--------------*/
/* Pink rect */
.pink {
    position: absolute;
    background-color: $pink-color;
    top: $pink-top-welcome-final;
    left: $pink-left-welcome-final;
    z-index: $pink-zindex;
}
.pink-transition-enter,
.pink-transition-appear {
    left: -47vw;
    top: $pink-top-welcome-final;
    z-index: $pink-zindex;
}
.pink-transition-enter-active,
.pink-transition-appear-active {
    top: $pink-top-welcome-final;
    left: $pink-left-welcome-final;
    z-index: $pink-zindex;
    transition: $slow-welcome-anim;
}
.pink-transition-enter-done,
