    top: $pink-top-welcome-final;
    left: $pink-left-welcome-final;
    z-index: 90;
}
.pink-transition-ontop-enter-active,
.pink-transition-ontop-appear-active {
    top: $top-middle;
    left: $left-middle;
    z-index: 90;
    transition: $fast-exit-anim;
}
.pink-transition-ontop-enter-done,
.pink-transition-ontop-appear-done {
    top: $top-middle;
    left: $left-middle;
    z-index: 90;
    transition: $fast-exit-anim;
}

/* Gather pink under*/
.pink-transition-under-enter,
.pink-transition-under-appear {
    top: $pink-top-welcome-final;
    left: $pink-left-welcome-final;
    z-index: $pink-zindex;
    background-color: $pink-color;
}
.pink-transition-under-enter-active,
